/* Purple line when hovering over (class="cool-link" (add to html for a purple line on hover)*/
.cool-link {
  display: inline-block;
  color: white;
  text-decoration: none;
}
.cool-link::after {
  content: '';
  display: block;
  width: 0;
  height: 2px;
  background: white;
  transition: width .3s;
}
.cool-link:hover::after {
  width: 100%;
  transition: width .3s;
}

